select {
  padding: 4px 20px;
  background: #f4f6f6;
  color: #000;
  width: 40vh;
  height: 27px;
  max-width: 147px;
  text-transform: capitalize;
  font-size: 12px;
}

.leaflet-top .info {
  width: 154px;
}

.profile-image {
  width: 78px;
  height: 78px;
  border-radius: 50%;
  margin: 0 auto;
  display: block;
}

.info p {
  line-height: 20px;
  font-family: monospace;

}

